.about {
	display: flex;
	height: 100%;
	width: 100%;
	align-items: center;
	justify-content: center;
  }
  
  .about-container {
	display: flex;
	align-items: center;
	height: 50%;
	width: 50%;
	justify-content: center;
  }
  
  .image-container {
	width: 30%;
	aspect-ratio: 1 / 1;
	border-radius: 50%;
  }
  
  .circle {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-image: url('./images/Profile.jpg');
	background-repeat: no-repeat;
    background-position: center; 
    position: relative;
  }
  
  .info-container {
	width: 70%;
	margin-left: 1rem;
	text-align: left;
}
  
  .name {
	font-size: 2rem;
	font-weight: bold;
  }
  
  .description {
	font-size: 1.2rem;
	margin-bottom: 1rem;
  }
  
  .button-container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: left;
  }
  
  .button {
	padding: 0.5rem 1rem;
	text-decoration: none;
	border-radius: 4px;
	margin-right: 1rem;
  }
  